import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Participantes_treinamento } from '../../../modelo/Participantes_treinamento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'participantes_treinamento-dados-edit',   templateUrl: 'participantes_treinamento_dadosEdit.component.html' }) export class Participantes_treinamentoDadosEditComponent implements OnInit {    participantes_treinamento: Participantes_treinamento;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.participantes_treinamento.Participantes_treinamento";                 let id = +params['id'];         this.participantes_treinamento = new Participantes_treinamento();         this.participantes_treinamento.cd_participantes_treinamento = id;         o.objetoJson = this.participantes_treinamento;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a participantes_treinamento. ';       }      this.participantes_treinamento = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


